import React, { useState, useEffect } from 'react'
import Grid from '@material-ui/core/Grid';
import TextField from "@material-ui/core/TextField";
import clsx from 'clsx';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';
import { updateAdditionalInformationAttention } from '../../api/api';
import { ToastContainer, toast } from 'react-toastify';
import { Button } from '@material-ui/core';
import { DropzoneArea } from 'material-ui-dropzone'



var _subtotal = 0;
var _administracion = 0;
var _imprevistos = 0;
var _utilidad = 0;
var _ivaSobreUtilidad = 0;

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
    },
    iconWidth: {
        width: 10
    },
    paper: {
        padding: theme.spacing(2),
        marginTop: 10,
    },
    button: {
        margin: theme.spacing(1),
    },
    input: {
        display: 'none',
    },
    fab: {
        margin: theme.spacing(1),
    },
    extendedIcon: {
        marginRight: theme.spacing(1),
    },
    icon: {
        marginRight: theme.spacing(1),
        color: 'black'
    },
}))
const styles = (theme) => ({
    root: {
        margin: 0,
        padding: theme.spacing(2),
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    }
});
const AttentionAdditionalInformationForm = props => {

    const classes = useStyles()
    useEffect(() => {
        if (props.attention) {
            setAttention(props.attention);
            setSubtotal(props.attention.subTotal);
            setImprevistos(props.attention.imprevistos);
            setAdministracion(props.attention.administracion);
            setUtilidad(props.attention.utilidad);
            setIvaSobreUtilidad(props.attention.ivaSobreUtilidad);
            setTotal(props.attention.total);
        }
    }, []);
    const [attention, setAttention] = useState(null)


    const [subtotal, setSubtotal] = useState("");
    const [administracion, setAdministracion] = useState("");
    const [imprevistos, setImprevistos] = useState("");
    const [utilidad, setUtilidad] = useState("");
    const [ivaSobreUtilidad, setIvaSobreUtilidad] = useState("");
    const [total, setTotal] = useState("");
    const [file, setFile] = useState({});

    const [errorSubtotal, setErrorSubtotal] = useState(false);
    const [errorTextSubtotal, setErrorTextSubtotal] = useState('');

    const [errorAdministracion, setErrorAdministracion] = useState(false);
    const [errorTextAdministracion, setErrorTextAdministracion] = useState('');

    const [errorImprevistos, setErrorImprevistos] = useState(false);
    const [errorTextImprevistos, setErrorTextImprevistos] = useState('');

    const [errorUtilidad, setErrorUtilidad] = useState(false);
    const [errorTextUtilidad, setErrorTextUtilidad] = useState('');

    const [errorIvaSobreUtilidad, setErrorIvaSobreUtilidad] = useState(false);
    const [errorTextIvaSobreUtilidad, setErrorTextIvaSobreUtilidad] = useState('');

    const [errorTotal, setErrorTotal] = useState(false);
    const [errorTextTotal, setErrorTextTotal] = useState('');

    const onSubmit = _ => {
        let errores = validateForm();

        if (errores === 0) {

            const formData = new FormData();
            formData.append("id", attention._id);
            formData.append("file", file);
            formData.append("subtotal", clearValue(subtotal));
            formData.append("administracion", clearValue(administracion));
            formData.append("imprevistos", clearValue(imprevistos));
            formData.append("utilidad", clearValue(utilidad));
            formData.append("ivaSobreUtilidad", clearValue(ivaSobreUtilidad));
            formData.append("total", clearValue(total));
            formData.append("action", props.action);

            updateAdditionalInformationAttention(formData).then(response => {
                console.log('response', response);
                if (response.status == 'success') {
                    props.onClose(false);

                    toast.info(response.message, {
                        position: "top-right",
                        autoClose: 5000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });

                    props.updateAttention(response.attention)

                } else {
                    toast.error(response.message, {
                        position: "top-right",
                        autoClose: 5000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                }

            });
        }
    }
    const onChangeSubTotal = e => {
        let value = formatNumber(e.target.value);
        _subtotal = clearValue(e.target.value)
        setSubtotal(value);
        validateSubtotal(e.target.value);

        sumTotal();
    }
    const onChangeAdministracion = e => {
        _administracion = clearValue(e.target.value)
        let value = formatNumber(e.target.value);
        setAdministracion(value);
        validateAdministracion(e.target.value);
        sumTotal();
    }
    const onChangeImprevistos = e => {
        _imprevistos = clearValue(e.target.value)
        let value = formatNumber(e.target.value);
        setImprevistos(value);
        validateImprevistos(e.target.value);
        sumTotal();
    }
    const onChangeUtilidad = e => {
        _utilidad = clearValue(e.target.value)
        let valueClear = clearValue(e.target.value)
        if (Number(valueClear) > 0) {
            let ivaSU = Number(valueClear) * 19 / 100;
            setIvaSobreUtilidad(formatNumber(clearValue(ivaSU.toString())));
            _ivaSobreUtilidad = clearValue(ivaSU.toString())
        }
        let value = formatNumber(e.target.value);
        setUtilidad(value);
        validateUtilidad(e.target.value);
        sumTotal();
    }
    const onChangeIvaSobreUtilidad = e => {
        _ivaSobreUtilidad = clearValue(e.target.value)
        let value = formatNumber(e.target.value);
        setIvaSobreUtilidad(value);
        validateIvaSobreUtilidad(e.target.value);
        sumTotal();
    }
    const clearValue = value => {
        value = value.replace('.', '');
        value = value.replace(',', '');

        return value
    }
    const formatNumber = value => {
        console.log('value', value)
        value = value.toString().replace(/\D/g, "")
            .replace(/([0-9])([0-9]{3})$/, '$1.$2')
            .replace(/\B(?=(\d{3})+(?!\d)\.?)/g, ",");
        return value
    }
    const sumTotal = () => {

        let suma = Number(_subtotal) + Number(_administracion) + Number(_imprevistos) + Number(_utilidad) + Number(_ivaSobreUtilidad)
        setTotal(formatNumber(suma))

    }

    const onChangeTotal = e => {
        let value = formatNumber(e.target.value);
        setTotal(value);
        validateTotal(e.target.value);
    }
    const validateSubtotal = (value) => {
        let text = value !== undefined ? value : subtotal

        if (text == null || text.toString().trim() === '') {
            setErrorSubtotal(true)
            setErrorTextSubtotal('Este campo es requerido')
            return 1
        }
        else {
            setErrorSubtotal(false)
            setErrorTextSubtotal('')
            return 0
        }
    }
    const validateAdministracion = (value) => {
        let text = value !== undefined ? value : administracion

        if (text == null || text.toString().trim() === '') {
            setErrorAdministracion(true)
            setErrorTextAdministracion('Este campo es requerido')
            return 1
        }
        else {
            setErrorAdministracion(false)
            setErrorTextAdministracion('')
            return 0
        }
    }
    const validateImprevistos = (value) => {
        let text = value !== undefined ? value : imprevistos

        if (text == null || text.toString().trim() === '') {
            setErrorImprevistos(true)
            setErrorTextImprevistos('Este campo es requerido')
            return 1
        }
        else {
            setErrorImprevistos(false)
            setErrorTextImprevistos('')
            return 0
        }
    }
    const validateUtilidad = (value) => {
        let text = value !== undefined ? value : utilidad

        if (text == null || text.toString().trim() === '') {
            setErrorUtilidad(true)
            setErrorTextUtilidad('Este campo es requerido')
            return 1
        }
        else {
            setErrorUtilidad(false)
            setErrorTextUtilidad('')
            return 0
        }
    }
    const validateIvaSobreUtilidad = (value) => {
        let text = value !== undefined ? value : ivaSobreUtilidad

        if (text == null || text.toString().trim() === '') {
            setErrorIvaSobreUtilidad(true)
            setErrorTextIvaSobreUtilidad('Este campo es requerido')
            return 1
        }
        else {
            setErrorIvaSobreUtilidad(false)
            setErrorTextIvaSobreUtilidad('')
            return 0
        }
    }
    const validateTotal = (value) => {
        let text = value !== undefined ? value : total

        if (text == null || text.toString().trim() === '') {
            setErrorTotal(true)
            setErrorTextTotal('Este campo es requerido')
            return 1
        }
        else {
            setErrorTotal(false)
            setErrorTextTotal('')
            return 0
        }
    }
    const validateForm = () => {
        let errors = 0
        errors += validateSubtotal()
        errors += validateAdministracion()
        errors += validateImprevistos()
        errors += validateUtilidad()
        errors += validateIvaSobreUtilidad()
        errors += validateTotal()
        return errors
    }
    const onFile = files => {
        setFile(files[0])
    }
    return (
        <Paper className={classes.paper}>
            <ToastContainer />

            <form encType={'multipart/form-data'}
                onSubmit={e => {
                    e.preventDefault()
                    onSubmit()
                }} >
                <Grid container spacing={3}>

                    <Grid item xs={12} sm={6}>
                        <TextField
                            fullWidth
                            error={errorSubtotal}
                            helperText={errorTextSubtotal}
                            className={clsx(classes.margin, classes.textField)}
                            variant="outlined"
                            label="Subtotal"
                            margin="dense"
                            value={subtotal}
                            onChange={onChangeSubTotal}
                        />
                    </Grid>

                    <Grid item xs={12} sm={6}>
                        <TextField
                            fullWidth
                            error={errorAdministracion}
                            helperText={errorTextAdministracion}
                            className={clsx(classes.margin, classes.textField)}
                            variant="outlined"
                            label="Administraci贸n"
                            margin="dense"
                            value={administracion}
                            onChange={onChangeAdministracion}
                        />
                    </Grid>

                    <Grid item xs={12} sm={6}>
                        <TextField
                            fullWidth
                            error={errorImprevistos}
                            helperText={errorTextImprevistos}
                            className={clsx(classes.margin, classes.textField)}
                            variant="outlined"
                            label="Imprevistos"
                            margin="dense"
                            value={imprevistos}
                            onChange={onChangeImprevistos}
                        />
                    </Grid>

                    <Grid item xs={12} sm={6}>
                        <TextField
                            fullWidth
                            error={errorUtilidad}
                            helperText={errorTextUtilidad}
                            className={clsx(classes.margin, classes.textField)}
                            variant="outlined"
                            label="Utilidad"
                            margin="dense"
                            value={utilidad}
                            onChange={onChangeUtilidad}
                        />
                    </Grid>

                    <Grid item xs={12} sm={6}>
                        <TextField
                            fullWidth
                            error={errorIvaSobreUtilidad}
                            helperText={errorTextIvaSobreUtilidad}
                            className={clsx(classes.margin, classes.textField)}
                            variant="outlined"
                            label="Iva sobre utilidad"
                            margin="dense"
                            value={ivaSobreUtilidad}
                            onChange={onChangeIvaSobreUtilidad}
                        />
                    </Grid>

                    <Grid item xs={12} sm={6}>
                        <TextField
                            fullWidth
                            error={errorTotal}
                            helperText={errorTextTotal}
                            className={clsx(classes.margin, classes.textField)}
                            variant="outlined"
                            label="Total"
                            margin="dense"
                            value={total}
                            onChange={onChangeTotal}
                        />
                    </Grid>

                    <Grid item xs={12} sm={12}>
                        <DropzoneArea
                            previewText="Selected files"
                            onChange={onFile}
                        />
                    </Grid>

                    <Grid item xs={2} sm={12}>
                        <Button type="submit" variant="contained" color='primary'>
                            {props.attention ? "Actualizar" : "Enviar"}
                        </Button>
                    </Grid>
                </Grid>
            </form>
        </Paper>)
}

export default AttentionAdditionalInformationForm