import React, { useState, useEffect, Fragment } from 'react'

import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

import AttentionDetail from './AttentionDetail'
import { Button } from '@material-ui/core';

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import SearchIcon from '@material-ui/icons/Search';
import Slide from '@material-ui/core/Slide';

import Tooltip from '@material-ui/core/Tooltip'
import TablePagination from '@material-ui/core/TablePagination';

import { withStyles } from '@material-ui/core/styles';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import { deleteAttention, openAttention, closeAttention, listAttentions, onDownloadAttention } from '../../api/api';
import Chip from '@material-ui/core/Chip';
import { AiOutlineLock, AiOutlineUnlock } from 'react-icons/ai';
import { IoIosSend } from 'react-icons/io';
import { BsClockHistory } from 'react-icons/bs';
import EditIcon from '@material-ui/icons/Edit';

import AttentionAdditionalInformationForm from "./AttentionAdditionalInformationForm";

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemIcon from '@material-ui/core/ListItemIcon';

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './attention.css';
import { AiOutlineCheckCircle, AiOutlineCloseCircle } from "react-icons/ai";
import { BsArrowCounterclockwise } from "react-icons/bs";
import { RiSendPlaneLine } from "react-icons/ri";
import { MdOutlineScheduleSend } from "react-icons/md";
import { VscEdit } from "react-icons/vsc";
import jwtDecode from 'jwt-decode';
import { AiOutlineCloudDownload } from "react-icons/ai";
import CircularProgress from '@material-ui/core/CircularProgress';
import config from "./../../config";
import { VscFilePdf } from "react-icons/vsc";

import moment from 'moment';
import AttentionForm from './AttentionForm';

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
    },
    iconWidth: {
        width: 10
    },
    paper: {
        padding: theme.spacing(2),
        marginTop: 10,
    },
    button: {
        margin: theme.spacing(1),
    },
    input: {
        display: 'none',
    },
    fab: {
        margin: theme.spacing(1),
    },
    extendedIcon: {
        marginRight: theme.spacing(1),
    },
    icon: {
        marginRight: theme.spacing(1),
        color: 'black'
    },
}))

const styles = (theme) => ({
    root: {
        margin: 0,
        padding: theme.spacing(2),
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    }
});

const DialogTitle = withStyles(styles)((props) => {
    const { children, classes, onClose, ...other } = props;
    return (
        <MuiDialogTitle disableTypography className={classes.root} {...other}>
            <Typography variant="h6">{children}</Typography>
            {onClose ? (
                <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                    <CloseIcon />
                </IconButton>
            ) : null}
        </MuiDialogTitle>
    );
});

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
})

const Attentions = _ => {

    const user = jwtDecode(localStorage.getItem('token'))

    useEffect(() => {

        setRoles(["5a046fe9627e3526802b3847"])
        let data = { start: 0, end: rowsPerPage, paginate: true }
        listAttentions(data).then(response => {
            setAttentions(response.attentions);
            setTotalAttentions(response.count);
        });
    }, []);

    const classes = useStyles()

    const [roles, setRoles] = useState([])
    const [attentions, setAttentions] = useState([])
    const [attention, setAttention] = useState(null)
    const [totalAttentions, setTotalAttentions] = useState(0)
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);

    const [open, setOpen] = useState(false);
    const [openSendCustomer, setOpenSendCustomer] = useState(false);
    const [openDetailAttention, setOpenDetailAttention] = useState(false);
    const [action, setAction] = useState("");
    const [openEditAttention, setOpenEditAttention] = useState(false);


    const [openHistory, setOpenHistory] = useState(false);

    const handleChangePage = (_, newPage) => {
        setPage(newPage);
        let data = { start: (newPage * rowsPerPage), end: rowsPerPage, paginate: true }
        listAttentions(data).then(response => {
            setAttentions(response.attentions);
            setTotalAttentions(response.count);
        });
    }
    const handleChangeRowsPerPage = event => {

        setRowsPerPage(+event.target.value);
        setPage(0);
        let data = { start: 0, end: event.target.value, paginate: true }
        listAttentions(data).then(response => {
            setAttentions(response.attentions);
            setTotalAttentions(response.count);
        });
    }
    const onCloseDeleteAttention = () => {
        setOpen(false);
    }
    const onDeleteAttention = id => {
        setOpen(false);

        deleteAttention(id).then(response => {
            if (response.status === 'success') {
                toast.success(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
                const newAttentions = attentions.map((currentAttention) => {
                    if (id !== currentAttention._id)
                        return currentAttention

                    currentAttention.status = "deleted";
                    return currentAttention
                })
                setAttentions(newAttentions)
            } else {
                toast.error(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
        }).catch(e => {

            toast.error('Ocurri贸 un error al eliminar la atenci贸n', {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        })

    }
    const onDetailAttention = attention => () => {
        setAttention(attention)
        setOpenDetailAttention(true)
    }
    const onOpenAttention = attention => () => {
        openAttention(attention._id).then(response => {
            if (response.status === 'success') {

                toast.success(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });

                const newAttentions = attentions.map((currentAttention) => {
                    if (attention._id !== currentAttention._id)
                        return currentAttention

                    currentAttention.status = "created";
                    return currentAttention
                })
                setAttentions(newAttentions)
            } else {
                toast.error(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
        }).catch(e => {
            console.log(e);
            toast.error("Occurri贸 un error al abrir la atenci贸n", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        });
    }
    const onCloseAttention = attention => () => {
        let data = { id: attention._id, status: "send" }
        closeAttention(data).then(response => {
            if (response.status === 'success') {

                toast.info(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
                const newAttentions = attentions.map((currentAttention) => {
                    if (attention._id !== currentAttention._id)
                        return currentAttention

                    currentAttention.status = "finished";
                    return currentAttention
                })
                setAttentions(newAttentions)
            } else {
                toast.error(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
        }).catch(e => {
            console.log(e);
            toast.error("Occurri贸 un error al cerrar la atenci贸n", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        });
    }
    const closeDetailAttention = () => {
        setOpenDetailAttention(false)
    }
    const onCloseSendCustomer = () => {
        setOpenSendCustomer(false);
    }
    const onSendCustomer = (attention, action) => () => {
        setAction(action)
        setAttention(attention)
        setOpenSendCustomer(true)
    }
    const downloadAttention = attention => () => {
        const newAttentions = attentions.map(currentAttention => {
            if (currentAttention._id === attention._id) {
                currentAttention.downloading = true;
            }
            return currentAttention;
        });
        setAttentions(newAttentions)

        let data = { id: attention._id }
        onDownloadAttention(data).then(response => {

            const newAttentions = attentions.map(currentAttention => {
                if (currentAttention._id === attention._id) {
                    currentAttention.downloading = false;
                }
                return currentAttention;
            });
            setAttentions(newAttentions)

            console.log(response)
            if (response.status === 'success') {

                toast.info(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
                setAttentions(newAttentions)
                window.open(`${config.urlPdf}${attention._id}.pdf`)
            } else {
                toast.error(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
        }).catch(e => {
            toast.error("Occurri贸 un error al descargar la atenci贸n", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        });
    }
    const onRejectCustomer = attention => () => {
        setAttention(attention)
        setOpenSendCustomer(true)
    }
    const onOpenModalHistory = attention => () => {
        setOpenHistory(true);
        setAttention(attention);
    }
    const onCloseModalHistory = () => {
        setOpenHistory(false);
    }
    const getStatus = item => {
        switch (item.statusSend) {
            case "send":
                return <div className='wrapper__history'>
                    <div className='statusHistory'>Enviado</div>
                    <div className='dateHistory'>{moment(item.date).format("YYYY-MM-DD HH:mm:ss")}</div>
                </div>


            case "resend":
                return <div className='wrapper__history'>
                    <div className='statusHistory'>Reenviado</div>
                    <div className='dateHistory'>{moment(item.date).format("YYYY-MM-DD HH:mm:ss")}</div>
                </div>

            case "reject":
                return <div className='wrapper__history'>
                    <div className='statusHistory'>Rechazado</div>
                    <div className='dateHistory'>{moment(item.date).format("YYYY-MM-DD HH:mm:ss")}</div>
                </div>

            case "accept":
                return <div className='wrapper__history'>
                    <div className='statusHistory'>Aceptado</div>
                    <div className='dateHistory'>{moment(item.date).format("YYYY-MM-DD HH:mm:ss")}</div>
                </div>
        }
    }
    const getIcon = statusSend => {
        switch (statusSend) {
            case "reject":
                return <AiOutlineCloseCircle style={{ fontSize: 32, color: "red" }} />

            case "send":
                return <RiSendPlaneLine style={{ fontSize: 32, color: "red" }} />

            case "accept":
                return <AiOutlineCheckCircle style={{ fontSize: 32, color: "green" }} />


            case "resend":
                return <MdOutlineScheduleSend style={{ fontSize: 32, color: "silver" }} />

        }

    }
    const getStatusSend = statusSend => {
        switch (statusSend) {
            case "accept":
                return <Chip label='Aceptada' size='small' color="primary" />

            case "reject":
                return <Chip label='Rechazada' size='small' />

            case "resend":
                return <Chip label='Reenviado' size='small' />

            case "send":
                return <Chip label='Enviado' size='small' />


        }
    }
    const updateAttention = attention => {
        console.log("updateAttention: ", updateAttention);
        const newAttentions = attentions.map((currentAttention) => {
            if (attention._id !== currentAttention._id)
                return currentAttention

            currentAttention = attention;
            return currentAttention
        })
        setAttentions(newAttentions)
    }

    const onEditAttention = attention => () => {
        setAttention(attention)
        setOpenEditAttention(true)
    }
    const onCloseAttentionForm = () => {
        setOpenEditAttention(false)
    }
    const onUpdateAttention = (name, description) => {

        const newAttentions = attentions.map((currentAttention) => {
            if (attention._id !== currentAttention._id)
                return currentAttention

            currentAttention.title = name
            currentAttention.description = description
            return currentAttention
        })
        setAttentions(newAttentions)
    }
    const onViewPdf = file => () => {
        window.open(`${config.urlPdf}${file}`)

    }
    return (
        <Fragment>
            <ToastContainer />
            <Paper className={classes.paper}>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>NOMBRE</TableCell>
                            <TableCell>DESCRIPCIN</TableCell>
                            <TableCell>ESTADO</TableCell>
                            <TableCell>CLIENTE</TableCell>
                            <TableCell>VER</TableCell>
                            <TableCell>ABRIR/CERRAR</TableCell>
                            <TableCell colSpan={5}>ACCIONES</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {
                            attentions.map(item => {
                                return (
                                    <TableRow key={item._id}>
                                        <TableCell>{item.title} </TableCell>

                                        <TableCell>{item.description} </TableCell>

                                        <TableCell>
                                            {
                                                item.status === 'finished' ?
                                                    <Chip style={{ margin: 1 }} label='Cerrado' size='small' variant="outlined" /> :
                                                    <Chip style={{ margin: 1 }} label='Abierto' size='small' color="primary" variant="outlined" />
                                            }
                                            {/* {
                                                item.statusSend === 'send' ?
                                                    <Chip style={{ margin: 1 }} label='Enviada' color='secondary' size='small' variant="outlined" /> :
                                                    ""
                                            } */}
                                            {
                                                getStatusSend(item.statusSend)
                                            }
                                        </TableCell>

                                        <TableCell>{item.customer ? item.customer.name : ""}</TableCell>

                                        <TableCell className={classes.iconWidth}>
                                            <Tooltip title="Detalle" placement="top">
                                                <SearchIcon className={classes.icon} onClick={onDetailAttention(item)} />
                                            </Tooltip>
                                        </TableCell>


                                        <TableCell className={classes.iconWidth}>
                                            {
                                                item.status === 'finished' ?
                                                    <div className='wrapper-icon-attention'>
                                                        <AiOutlineUnlock className='icon' onClick={onOpenAttention(item)} />
                                                        <span className='open-close'>Abrir</span>
                                                    </div> :
                                                    <div className='wrapper-icon-attention'>
                                                        <AiOutlineLock className='icon' onClick={onCloseAttention(item, "send")} />
                                                        <div className='open-close'>Cerrar</div>
                                                    </div>
                                            }
                                        </TableCell>

                                        <TableCell className={classes.iconWidth}>
                                            {
                                                item.statusSend == "pending" ?
                                                    <Tooltip title="Enviar cliente" placement="top">
                                                        <div>
                                                            <IoIosSend className={"icon"} onClick={onSendCustomer(item, "create")} />
                                                        </div>
                                                    </Tooltip> : ""
                                            }
                                        </TableCell>

                                        <TableCell className={classes.iconWidth}>
                                            {
                                                roles.includes(user.role) && item.statusSend == 'reject' ?
                                                    <Tooltip title="Editar" placement="top">
                                                        <div>
                                                            <VscEdit className={"icon"} onClick={onSendCustomer(item, "update")} />
                                                        </div>
                                                    </Tooltip> : ""

                                            }
                                            <Tooltip title="Descargar" placement="top">
                                                {
                                                    item.downloading ?
                                                        <div className='circularProgress'>
                                                            <CircularProgress size={16} />
                                                        </div> :
                                                        <div>
                                                            <AiOutlineCloudDownload className={"icon"} onClick={downloadAttention(item)} />
                                                        </div>
                                                }
                                            </Tooltip>
                                        </TableCell>


                                        <TableCell className={classes.iconWidth}>
                                            {
                                                item.descriptions.length > 0 ? <Tooltip title="Hisotoria" placement="top">
                                                    <div>
                                                        <BsClockHistory className={"icon"} onClick={onOpenModalHistory(item)} />
                                                    </div>
                                                </Tooltip> : ""
                                            }
                                        </TableCell>

                                        <TableCell className={classes.iconWidth}>
                                            <Tooltip title="Editar" placement="top">
                                                <EditIcon className={classes.icon} onClick={onEditAttention(item)} />
                                            </Tooltip>
                                        </TableCell>

                                        {
                                            item.file ?
                                                <TableCell className={classes.iconWidth}>
                                                    <Tooltip title="Pdf" placement="top">
                                                        <VscFilePdf className={"icon"} onClick={onViewPdf(item.file)} />
                                                    </Tooltip>
                                                </TableCell> :
                                                <TableCell className={classes.iconWidth}></TableCell>
                                        }

                                    </TableRow>)
                            })
                        }
                    </TableBody>
                </Table>
                <TablePagination
                    rowsPerPageOptions={[10, 25, 100]}
                    component="div"
                    count={totalAttentions}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onChangePage={handleChangePage}
                    onChangeRowsPerPage={handleChangeRowsPerPage}
                />
            </Paper>

            <Dialog
                open={openSendCustomer}
                onClose={onCloseSendCustomer}>
                <DialogTitle style={{ cursor: 'move' }} >
                    ENVIAR AL CLIENTE
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        <AttentionAdditionalInformationForm
                            action={action}
                            attention={attention}
                            updateAttention={updateAttention}
                            onClose={onCloseSendCustomer}
                        />
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseSendCustomer} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                open={open}
                onClose={onCloseDeleteAttention}
                aria-labelledby="draggable-dialog-title">
                <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                    ELIMINAR ATENCION
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Esta seguro de eliminar esta atenci贸n?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseDeleteAttention} color="primary">
                        NO
                    </Button>
                    <Button onClick={onDeleteAttention} color="primary">
                        SI
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                fullWidth maxWidth="lg"
                open={openDetailAttention}
                onClose={closeDetailAttention}
                TransitionComponent={Transition}>
                <DialogContent>
                    <AttentionDetail attention={attention} />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={closeDetailAttention} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                open={openHistory}
                onClose={onCloseModalHistory}>
                <DialogTitle >
                    Historia
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        <List className={classes.root}>
                            {
                                attention ?
                                    attention.descriptions.map(item => {
                                        return (
                                            <ListItem alignItems="flex-start">
                                                <ListItemIcon>
                                                    {getIcon(item.statusSend)}
                                                </ListItemIcon>
                                                <ListItemText
                                                    primary={getStatus(item)}
                                                    secondary={item.description}
                                                />
                                            </ListItem>

                                        );
                                    })
                                    : ""
                            }
                        </List>
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseModalHistory} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog maxWidth="md" open={openEditAttention} onClose={onCloseAttentionForm} TransitionComponent={Transition}>
                <DialogContent>
                    <AttentionForm onCloseAttentionForm={onCloseAttentionForm} onUpdateAttention={onUpdateAttention} attention={attention} />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseAttentionForm} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

        </Fragment>)
}

export default Attentions