import React, { Fragment, useState, useEffect } from 'react'
import { emphasize, makeStyles } from '@material-ui/core/styles'
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid'
import Container from '@material-ui/core/Container'
import clsx from 'clsx'
import { Button } from '@material-ui/core'

import PropTypes from "prop-types"
import TextField from "@material-ui/core/TextField"
import { updateAttentionWeb } from '../../api/api';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: 250,
    minWidth: 290
  },
  input: {
    display: "flex",
    padding: 0,
    height: "auto"
  },
  valueContainer: {
    display: "flex",
    flexWrap: "wrap",
    flex: 1,
    alignItems: "center",
    overflow: "hidden"
  },
  chip: {
    margin: theme.spacing(0.5, 0.25)
  },
  chipFocused: {
    backgroundColor: emphasize(
      theme.palette.type === "light"
        ? theme.palette.grey[300]
        : theme.palette.grey[700],
      0.08
    )
  },
  noOptionsMessage: {
    padding: theme.spacing(1, 2)
  },
  singleValue: {
    fontSize: 16
  },
  placeholder: {
    position: "absolute",
    left: 2,
    bottom: 6,
    fontSize: 16
  },

  divider: {
    height: theme.spacing(2)
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,

  },
  button:
  {
    margin: theme.spacing(1),
  },
  fab:
  {
    marginTop: -2,
    float: "right"
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
  formControl: {
    margin: theme.spacing(1),
  },
}))

function inputComponent({ inputRef, ...props }) {
  return <div ref={inputRef} {...props} />
}

inputComponent.propTypes = {
  inputRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({
      current: PropTypes.any.isRequired
    })
  ])
}
const AttentionForm = props => {

  useEffect(() => {
    console.log(props)


    if (props.attention) {

      setName(props.attention.title);
      setDescription(props.attention.description);

    }
  }, [])

  const [disabled, setDisabled] = useState(false)

  const [file, setFile] = useState({})
  const classes = useStyles()


  const [name, setName] = useState('');
  const [description, setDescription] = useState('');

  const [errorName, setErrorName] = useState(false)
  const [errorTextName, setErrorTextName] = useState('')

  const [errorDescription, setErrorDescription] = useState(false)
  const [errorTextDescription, setErrorTextDescription] = useState('')


  const onChangeName = e => {
    setName(e.target.value);
    validateName(e.target.value);
  }
  const onChangeDescription = e => {
    setDescription(e.target.value);
    validateDescription(e.target.value);
  }
  const validateForm = () => {
    let errors = 0
    errors += validateName()
    errors += validateDescription()
    // errors += validatePrice()
    // errors += validateQuantity()
    return errors
  }
  const validateName = (value) => {
    let text = value !== undefined ? value : name

    if (text == null || text.toString().trim() === '') {
      setErrorName(true)
      setErrorTextName('Este campo es requerido')
      return 1
    }
    else {
      setErrorName(false)
      setErrorTextName('')
      return 0
    }
  }
  const validateDescription = (value) => {
    let text = value !== undefined ? value : description

    if (text == null || text.toString().trim() === '') {
      setErrorDescription(true)
      setErrorTextDescription('Este campo es requerido')
      return 1
    }
    else {
      setErrorDescription(false)
      setErrorTextDescription('')
      return 0
    }
  }

  const onSubmit = _ => {

    let errores = validateForm()


    if (errores === 0) {
      const formData = new FormData();
      if (props.attention) {
        formData.append("id", props.attention._id);
      }
      formData.append("name", name);
      formData.append("description", description);

      if (!props.attention) {

      } else {

        updateAttentionWeb(formData).then(response => {
          console.log('response', response);
          if (response.status == 'success') {
            toast.info(response.message, {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });

            props.onUpdateAttention(name, description);
            props.onCloseAttentionForm()

          } else {
            toast.error('Ocurri贸 un error al actualizar la atenci贸n', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
          }

        });
      }
    } else {
      toast.error('Error', {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  }


  return (
    <Fragment>
      <ToastContainer />
      <Container maxWidth="md">

        <Paper className={classes.paper}>
          <form encType={'multipart/form-data'}
            onSubmit={e => {
              e.preventDefault()
              onSubmit()
            }} >
            <Grid container spacing={3}>

              <Grid item xs={12} sm={12}>
                <TextField
                  fullWidth
                  multiline
                  error={errorName}
                  helperText={errorTextName}
                  margin="dense"
                  autoComplete='off'
                  id="outlined-simple-start-adornment"
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Nombre"
                  value={name}
                  onChange={onChangeName} />
              </Grid>

              <Grid item xs={12} sm={12}>
                <TextField
                  fullWidth
                  multiline
                  error={errorDescription}
                  helperText={errorTextDescription}
                  margin="dense"
                  autoComplete='off'
                  id="outlined-simple-start-adornment"
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Descripci贸n"
                  value={description}
                  onChange={onChangeDescription} />
              </Grid>


              <Grid item xs={2} sm={3}>
                <Button disabled={disabled} type="submit" variant="contained" color='primary'>
                  {props.attention ? "Actualizar" : "Guardar"}
                </Button>
              </Grid>
            </Grid>
          </form>
        </Paper>

      </Container>
    </Fragment>
  )
}

export default AttentionForm